body, html {
  background-color: #D8F0FF;
  min-height: 100%;
  margin: 0;
  padding: 0; }

@media screen and (max-width: 1500px) {
  .zoom_img_icon {
    right: 15px !important; }
  .item_about_scroll {
    right: 40px !important; } }

@media screen and (max-width: 992px) {
  .zoom_img_icon {
    display: none !important; } }

@media screen and (max-width: 400px) {
  .close_wrap_state_info,
  .open_wrap_state_info {
    width: 33px !important;
    line-height: 24px !important;
    border-radius: 15px !important; }
  .wrap_state_info {
    z-index: 50 !important; }
  .about_row {
    opacity: 0.8; } }

#chartdiv {
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  border: 1px; }

.hz_line {
  position: absolute;
  display: block;
  height: 1px;
  width: 340px;
  background-color: #D4D3D3;
  margin-left: 7px; }

.wrap_state_info {
  position: absolute;
  display: block;
  width: 0;
  height: 100%;
  left: -5px;
  top: 0;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.9);
  -webkit-box-shadow: 1px 0 3px 2px #999999;
  box-shadow: 1px 0 3px 2px #999999;
  -webkit-transition: 0.3s ease-in;
  -o-transition: 0.3s ease-in;
  transition: 0.3s ease-in; }

.wrap_state_info ul {
  list-style: none;
  padding: 10px; }

.wrap_state_info li {
  font-family: 'Lexend Deca', sans-serif; }

.li_info {
  font-size: 14px;
  margin: 5px 0 0 8px; }

.li_info span {
  font-size: 18px;
  font-weight: bold; }

.wrapper_state_info {
  width: 430px;
  height: 720px;
  padding: 10px;
  overflow-x: hidden;
  position: absolute;
  overflow-y: scroll; }

.close_wrap_state_info {
  z-index: 1000;
  display: none;
  position: absolute;
  width: 33px;
  height: 30px;
  top: 5px;
  left: 5px;
  line-height: 18px;
  cursor: pointer;
  font-size: 35px;
  text-align: center;
  border: 1px solid #D4D4D4;
  border-radius: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.close_wrap_state_info:hover {
  font-size: 38px;
  border: 1px solid #FFFFFF; }

.open_wrap_state_info {
  z-index: 1000;
  display: none;
  position: absolute;
  width: 33px;
  height: 30px;
  top: 5px;
  left: 5px;
  line-height: 18px;
  cursor: pointer;
  font-size: 35px;
  text-align: center;
  border: 1px solid #D4D4D4;
  border-radius: 20px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.li_country_photo {
  display: block;
  width: 362px;
  height: 260px;
  margin: -20px 0 20px -8px;
  -webkit-box-shadow: 1px 0 8px 1px black;
  box-shadow: 1px 0 8px 1px black; }

.country_photo {
  width: 366px;
  height: 260px;
  margin: 0px 0 10px -4px; }

.img_loader {
  display: inline-block;
  position: absolute;
  display: none;
  margin-top: 70px;
  width: 64px;
  height: 64px; }

.img_loader span {
  display: inline-block;
  position: absolute;
  left: 6px;
  width: 13px;
  background: #74B266;
  -webkit-box-shadow: 1px 0 1px 0 #9C9C9C;
  box-shadow: 1px 0 1px 0 #9C9C9C;
  opacity: 0.8;
  -webkit-animation: img_loader_anim 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  animation: img_loader_anim 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite; }

.img_loader span:nth-child(1) {
  left: 6px;
  -webkit-animation-delay: -0.24s;
  animation-delay: -0.24s; }

.img_loader span:nth-child(2) {
  left: 26px;
  -webkit-animation-delay: -0.12s;
  animation-delay: -0.12s; }

.img_loader span:nth-child(3) {
  left: 45px;
  -webkit-animation-delay: 0;
  animation-delay: 0; }

@-webkit-keyframes img_loader_anim {
  0% {
    top: 6px;
    height: 51px; }
  50%,
  100% {
    top: 19px;
    height: 26px; } }

@keyframes img_loader_anim {
  0% {
    top: 6px;
    height: 51px; }
  50%,
  100% {
    top: 19px;
    height: 26px; } }

.zoom_img_icon {
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
  top: 8px;
  background-image: url("../img/zoom_icon.png");
  background-size: cover;
  right: 10px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.rowz {
  position: absolute;
  display: block;
  min-height: 35px;
  width: 90% !important; }

.country_flag {
  display: block;
  position: relative;
  max-width: 100px;
  /*	margin-left:100px;*/
  height: 50px;
  margin-bottom: 20px;
  -webkit-box-shadow: 1px 0 3px 0 black;
  box-shadow: 1px 0 3px 0 black; }

.li_info_about {
  padding-top: 20px; }

.li_about {
  height: 300px;
  width: 370px;
  padding-right: 30px;
  overflow-x: hidden;
  text-align: left;
  position: relative; }

.item_about_scroll {
  display: block;
  width: 20px;
  height: 20px;
  position: relative;
  opacity: 0.9;
  float: right;
  right: 30px;
  background-image: url("../img/down_arr.png");
  background-size: cover; }

.li_about .item_about_label {
  padding: 10px 0 30px 5px;
  font-style: italic;
  cursor: text;
  font-size: 13px; }

.li_info_timezone {
  min-height: 55px;
  word-wrap: break-word;
  width: 330px;
  padding-bottom: 20px; }

.span_timezone {
  font-size: 13px !important; }

.li_info_area span:nth-child(2) {
  font-size: 13px;
  font-weight: normal; }

.bgdrop_modal {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999; }

#country_photo_viewer {
  position: absolute;
  z-index: 1000;
  display: block;
  -webkit-box-shadow: 1px 0 5px 0 black;
  box-shadow: 1px 0 5px 0 black;
  top: 40px;
  height: 640px; }

#country_photo_viewer img {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0; }

.wiki_link {
  font-size: 10px !important;
  color: #0737EA;
  text-decoration: underline;
  cursor: pointer;
  font-weight: norma !important;
  margin: 5px 0 45px 5px;
  position: relative; }

.about_row {
  display: block;
  position: absolute;
  z-index: 40;
  min-width: 200px;
  min-height: 20px;
  bottom: 4px;
  right: 0;
  font-family: 'Lexend Deca', sans-serif;
  font-size: 14px;
  text-align: center; }

.about_row a {
  text-decoration: none;
  color: black; }

.about_row span:nth-child(1) {
  font-size: 12px; }

.about_row span:nth-child(2) {
  color: #0737EA;
  text-decoration: underline;
  cursor: pointer; }

.about_div {
  position: absolute;
  display: block;
  width: 250px;
  height: 350px;
  right: 30px;
  bottom: 30px;
  z-index: 999; }

.close_about {
  display: block;
  position: absolute;
  height: auto;
  line-height: 13px;
  right: 5px;
  top: 5px;
  font-size: 20px;
  cursor: pointer; }

.close_about:hover {
  font-weight: bold; }

.about_div_bubble {
  position: relative;
  display: block;
  width: 250px;
  height: 320px;
  background-color: white;
  -webkit-box-shadow: 1px 0 3px 0 black;
  box-shadow: 1px 0 3px 0 black;
  border-radius: 4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  left: 20px; }

.about_div_bubble:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-top-color: white;
  border-bottom: 0;
  border-right: 0;
  margin-left: 60px;
  margin-bottom: -20px; }

.about_div_bubble img {
  position: relative;
  width: 200px;
  height: 150px;
  border-bottom: 1px solid #D8D8D8;
  top: 10px;
  left: 25px;
  padding-bottom: 20px;
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in; }

.about_div_bubble img:hover {
  -webkit-transform: scale(1.03);
  -ms-transform: scale(1.03);
  transform: scale(1.03); }

.about_txt {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  position: absolute;
  top: 165px;
  text-align: center;
  height: 150px;
  width: 100%;
  left: 0; }

.about_txt p {
  text-decoration: underline; }

.about_txt span {
  font-size: 14px;
  margin: 2px; }
